import React, { Component } from 'react'
import PropTypes from 'prop-types'
import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import AccessTime from "@material-ui/icons/AccessTime";
import HomeIcon from '@material-ui/icons/Home';
import AirlineSeatReclineNormalIcon from '@material-ui/icons/AirlineSeatReclineNormal';
import { withStyles } from "@material-ui/core/styles";
import Scrollbar from "react-scrollbars-custom";
class CustomerInfo extends Component {
    static propTypes = {
        prop: PropTypes
    }
    constructor(props) {
        super(props)
        this.myRef = React.createRef()
        this.state = {
            cui_ve : []
        }
    }
    
    componentDidMount(){
        this.setState({cui_ve:this.props.cui_ve})
    }
    static getDerivedStateFromProps(props, state) {
        if (props.cui_ve !== state.cui_ve) {
            return {
                cui_ve: props.cui_ve
            }
        }
    }

    render() {
        const { classes } = this.props;
        const customer = this.state.cui_ve.map((row,index) =>{
            
            let row1 = row.customer_info
            console.log('row1',row1)
            return (row1 != null)?<div key={index}>
                        <CardBody>
                            <h4 className={classes.cardTitle}>Họ Tên: {row1.name}</h4>
                            <h4 className={classes.cardTitle}>DT: {row1.phone}</h4>
                            <p className={classes.cardCategory}> <AirlineSeatReclineNormalIcon /> {row.label} ,  <HomeIcon /> {row1.address}</p>
                        </CardBody>
                        <CardFooter chart>
                            <div className={classes.stats}>
                        
                            </div>
                        </CardFooter>
                    </div>:""
        })
        return (
            <Card chart  >
                    <CardHeader color="danger">
                    <h3 className={classes.cardTitle}>Thông tin đặt vé</h3>
                    </CardHeader>
                    <Scrollbar style={{height: '100vh' }}>
                        {customer}
                    </Scrollbar>
                    
            </Card>
        )
    }
}
export default withStyles(styles)(CustomerInfo)